.header {
  $this: &;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  min-width: 320px;
  z-index: 7;
  background: #fff;
  @include tr(transform .3s ease-in-out);
  &.is-menu-open {
    #{$this}__btn {
      opacity: 0; }
    #{$this}__menu {
      display: block; }
    #{$this}__soc {
      display: block; } }
  &.is-scroll {
    transform: translateY(-41px);
    #{$this}__nav {
      .nav__link {
        padding: 30px 0; } } }
  &__top {
    padding: 10px 0;
    border-bottom: 1px solid $green;
    overflow: hidden; }
  &__link {
    display: inline-block;
    font-size: 11px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: $black;
    line-height: 1.9;
    @include tr(opacity .2s);
    @include ubuntu-l;
    &:not(:last-child) {
      border-right: 1px solid $green;
      padding-right: 38px;
      margin-right: 37px;
      @include sm {
        padding-right: 19px;
        margin-right: 16px; } }
    &:last-child {
      @include sm {
        font-size: 10px; }
      @include xs {
        margin-right: 0; } }
    &:hover {
      opacity: 0.7; } }
  &__logo {
    margin-top: -8px; }
  &__menu {
    @include md {
      display: none;
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background: #fff;
      z-index: 9;
      text-align: center;
      height: calc(100vh - 107px);
      overflow: hidden;
      overflow-y: auto;
      padding-bottom: 40px; }
    .icon-decor-line-bottom {
      fill: $green;
      max-width: 96px;
      margin-bottom: 20px; }
    .soc {
      &.soc_sm {
        margin: 0 -10px;
        .soc__item {
          margin: 0 10px; } } } }
  &__soc {
    display: none; }
  &__menu-hidden {
    margin-bottom: 50px;
    @include rmin(980) {
      display: none; } }
  &__content {
    position: relative; }
  &__btn {
    @include md {
      margin-left: -70px; }
    @include xs {
      display: none; } } }

.burger {
  position: relative;
  display: block;
  width: 32px;
  height: 14px;
  @include rmin(980) {
    display: none; }
  &.is-active {
    width: 20px;
    span:first-child {
      transform: rotate(45deg);
      top: 6px; }
    span:last-child {
      transform: rotate(-45deg);
      top: 6px; }
    span:nth-child(2) {
      transform: scale(0); } }
  > span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: $orange;
    @include tr(transform .3s ease-in-out);
    &:first-child {
      top: 0; }
    &:nth-child(2) {
      top: 6px; }
    &:last-child {
      top: 12px; } } }
