// fonts

@include font('LoraRegular', 'LoraRegular');
@include font('LoraItalic', 'LoraItalic');
@include font('LoraBoldItalic', 'LoraBoldItalic');
@include font('LoraBold', 'LoraBold');

@include font('UbuntuLight', 'UbuntuLight');
@include font('UbuntuRegular', 'UbuntuRegular');
@include font('UbuntuBold', 'UbuntuBold');


@mixin lora {
  font-family: "LoraRegular", Arial, Helvetica, serif; }
@mixin lora-i {
	font-family: "LoraItalic", Arial, Helvetica, serif; }
@mixin lora-bi {
	font-family: "LoraBoldItalic", Arial, Helvetica, serif; }
@mixin lora-b {
  font-family: "LoraBold", Arial, Helvetica, serif; }

@mixin ubuntu {
  font-family: "UbuntuRegular", Arial, Helvetica, serif; }
@mixin ubuntu-l {
  font-family: "UbuntuLight", Arial, Helvetica, serif; }
@mixin ubuntu-b {
  font-family: "UbuntuBold", Arial, Helvetica, serif; }


body {
  background: #FFF;
  line-height: 1.2;
  font-size: 14px;
  min-width: 320px;
  color: $black;
  padding-top: 141px;
  @include lora;
  @include smooth-font;
  @include md {
    padding-top: 107px; }
  &.is-hidden {
    overflow: hidden;
    @include md {
      width: 100%;
      height: 100%;
      position: fixed;
      z-index: -1; } } }

a {
	text-decoration: none; }

button {
	background: none; }

img {
  max-width: 100%; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.text-left {
	text-align: left; }

.text-orange {
  color: $orange; }

.upper {
	text-transform: uppercase; }

.bg-gray {
  background: $gray;
  min-height: 100%;
  .section {
    &:first-child {
      margin-bottom: 145px;
      @include md {
        margin-bottom: 100px; } } } }

.bg-black {
  background: $black; }
