.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background: rgba(48, 61, 74, 0.8);
  white-space: nowrap;
  padding: 40px 20px;
  transition: opacity .3s ease,visibility .3s ease;
  overflow: auto;
  text-align: center;
  font-size: 0;
  min-width: 320px;
  border: 1px solid #fff;
  text-align: center;
  font-size: 0;
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup.is-active {
  opacity: 1;
  visibility: visible;
}

.popup__close {
  position: absolute;
  right: 40px;
  top: 40px;
  z-index: 3;
  transition: opacity 0.3s;
}

.popup__close:hover {
  opacity: .8;
}

@media only screen and (max-width: 767px) {
  .popup__close {
    right: 20px;
    top: 20px;
  }
}

.popup__close .icon-close {
  width: 14px;
  height: 14px;
  fill: #f08058;
}

.popup__container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 970px;
  white-space: normal;
  background: #fff;
  padding: 40px;
  font-size: 14px;
  text-align: left;
  position: relative;
}

.popup__container:after, .popup__container:before {
  content: '';
  position: absolute;
  background: #000;
  opacity: 0.05;
}

.popup__container:after {
  right: -10px;
  top: 10px;
  bottom: 0;
  width: 10px;
}

.popup__container:before {
  left: 10px;
  right: -10px;
  bottom: -10px;
  height: 10px;
}

